*,
*::after,
*::before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background: #252329;
  width: 100vw;
  max-width: 100%;
  min-height: 100vh;
  line-height: 1.5;
  font-family: 'Noto Sans', sans-serif;
  color: rgba(224, 224, 224, 1);
}

img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}

input,
button,
textarea,
select {
  font: inherit;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}

a {
  text-decoration: none;
  color: inherit;
}